import React, {Fragment} from "react"
import {graphql} from "gatsby";
import "../common/main.css";

const Layout = ({data}) => {
    console.log(data);
    const url = data.url.edges[0].node.publicURL;
    const thumbNail = data.thumbNail.edges[0].node.publicURL;
    const logos = data.logos.edges;

    console.log(url);
    return (
        <Fragment>
            <div
                className="parallax-1"
            >
                <div>
                    <div
                        className="folder-top">
                        <div>
                            <img
                                className="thumbnail-big"
                                src={thumbNail}/>
                        </div>
                        <div
                            className="folder-top-text"
                        >
                            <h3>Hello There</h3>
                            <h4>I'm Rachita Bansal</h4>
                            <h4>I'm a software engineer</h4>
                            <h4>building simple and robust UI</h4>
                            <div className="thumbnail-small">
                                {
                                    logos.map(({node}, index) => (
                                        <a key={index}
                                          className="link"
                                           href="https://medium.com/@rachitabansal">
                                            <img
                                                key={index}
                                                className="logo-img"
                                                border="0"
                                                alt="medium"
                                                src={node.publicURL}
                                                width="30"
                                                height="30"
                                            />
                                        </a>
                                    ))
                                }
                            </div>
                        </div>
                    </div>
                    <div
                        className="folder-right">
                        <div>
                            <h1>Today ...</h1>
                            <p>
                                I'm collaborating with cross-functional teams to build a scalable, robust and cost-effective web interface.

                            </p>
                        </div>
                    </div>
                </div>
            </div>
            <div
                className="parallax-2"
            >
                <div>
                    <div
                        className="folder-right"
                    >
                        <div>
                            <h1>
                                Yesterday ...
                            </h1>
                        </div>
                    </div>
                </div>
            </div>
            <div
                className="parallax-3"
            >
                <div>
                    <div
                       className="folder-right"
                    >
                        <div>
                            <h1>
                                These are my adventures ...
                            </h1>
                        </div>
                    </div>
                </div>
            </div>
        </Fragment>
    )
};

export const query = graphql`
    query{
        url: allFile(filter: { name: { eq: "image6" } }){
            edges {
                node {
                    publicURL
                }
            }
        }
        thumbNail: allFile(filter: { name: { eq: "image5" } }){
            edges {
                node {
                    publicURL
                }
            }
        }
        logos: allFile(filter: { extension: { eq: "png" } }){
            edges {
                node {
                    publicURL
                }
            }
        }
    }
`;
export default Layout;
